import { required, integer, email, maxLength } from "vuelidate/lib/validators";

export const personQuestionsMixin = {
  data() {
    return {
      person: {
        id: this.personInit ? this.personInit.id : "",
        firstName: this.personInit ? this.personInit.firstName : "",
        lastName: this.personInit ? this.personInit.lastName : "",
        email: this.personInit ? this.personInit.email : "",
        data: this.personInit ? this.personInit.data : {}
      }
    };
  }
};

export const personQuestionsValidations = {
  firstName: { required },
  lastName: { required },
  email: { required, email }
};

export const applicationQuestionsMixin = {
  data() {
    return {
      applicationQuestions: {
        yearOfBirth: this.personInit ? this.personInit.data.yearOfBirth : "",
        gender: this.personInit ? this.personInit.data.gender : "",
        eventsAttended:
          this.personInit &&
          this.personInit.data &&
          this.personInit.data.eventsAttended
            ? this.personInit.data.eventsAttended
            : [],
        occupation: this.personInit ? this.personInit.data.occupation : "",
        organisation: this.personInit ? this.personInit.data.organisation : "",
        industry: this.personInit ? this.personInit.data.industry : "",
        educationLevel: this.personInit
          ? this.personInit.data.educationLevel
          : ""
      }
    };
  }
};

export const applicationQuestionsValidations = {
  yearOfBirth: { required, integer },
  gender: { required },
  eventsAttended: {},
  occupation: { required },
  organisation: { required },
  industry: { required },
  educationLevel: { required }
};

export const ticketQuestionsMixin = {
  data() {
    return {
      ticketQuestions: {
        address: this.personInit ? this.personInit.data.address : {},
        foundedYesNo: this.personInit ? this.personInit.data.foundedYesNo : "",
        founded: this.personInit ? this.personInit.data.founded : "",
        dietaryRequirements:
          this.personInit &&
          this.personInit.data &&
          this.personInit.data.dietaryRequirements
            ? this.personInit.data.dietaryRequirements
            : [],
        accessabilityRequirements:
          this.personInit &&
          this.personInit.data &&
          this.personInit.data.accessabilityRequirements
            ? this.personInit.data.accessabilityRequirements
            : [],
        conversationStarters: {
          one: this.personInit.data.conversationStarters
            ? this.personInit.data.conversationStarters.one
            : "",
          two: this.personInit.data.conversationStarters
            ? this.personInit.data.conversationStarters.two
            : ""
        }
      }
    };
  }
};

export const ticketQuestionsValidations = [
  {
    address: { required },
    foundedYesNo: { required },
    founded: {},
    dietaryRequirements: {},
    accessabilityRequirements: {},
    conversationStarters: {
      one: { required, maxLength: maxLength(40) },
      two: { required, maxLength: maxLength(40) }
    }
  },
  {
    address: { required },
    foundedYesNo: { required },
    founded: { required },
    dietaryRequirements: {},
    accessabilityRequirements: {},
    conversationStarters: {
      one: { required, maxLength: maxLength(40) },
      two: { required, maxLength: maxLength(40) }
    }
  }
];

